ontainer .project .technologies-container{display:flex;align-items:center;width:100%;flex-wrap:wrap;color:#bebebe;flex-grow:0;padding-bottom:16px}@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3){.projects-component .projects-container .project .technologies-container{justify-content:center}}.projects-component .projects-container .project .technologies-container .technology{font-family:"Source Code Pro", monospace;font-size:small;padding-right:16px}@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3){.projects-component .projects-container .project .technologies-container .technology{font-size:24px}}.projects-component .projects-container .project .git-link{align-self:center;border-radius:50%;margin-top:16px}.projects-component .projects-container .project .git-link .icon{width:30px;height:30px}@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3){.projects-component .projects-container .project .git-link .icon{width:60px;height:60px}}

